html {
  height: 100%;
  width: 100%;
  position: fixed;
}

body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
}

.panel {
  position: absolute;
  bottom: 0;
  left: 10px;
  right: 10px;
  height: 100px;
  background: #efebe9;
  border: solid 5px #a1887f;
  border-bottom: none;
  border-radius: 10px 10px 0 0 ;
}

.controls {
  position: absolute;
}
.controls .Dice {
  position: absolute;
}

.controls-0 {
  bottom: 64%;
  left: 64%;
  right: 0;
  top: 0;
  border-radius: 0 0 0 50%;
  background: rgba(255, 0, 0, 0.25);
}
.controls-0 .Dice {
  bottom: 20%;
  left: 20%;
}

.controls-1 {
  bottom: 0;
  left: 64%;
  right: 0;
  top: 64%;
  border-radius: 50% 0 0 0;
  background: rgba(0, 0, 255, 0.25);
}
.controls-1 .Dice {
  top: 20%;
  left: 20%;
}

header.title {
  color: #c0ca33;
  font-family: "Permanent Marker", cursive;
  font-size: 24px;
  text-align: center;
}

.BoardWrapper {
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
  margin: 80px auto;
}
.BoardWrapper .PlayerName1 {
  position: absolute;
  top: 0px;
  right: 20px;
}
.BoardWrapper .PlayerName2 {
  position: absolute;
  bottom: 0px;
  right: 20px;
}
.BoardWrapper .PlayerName3 {
  position: absolute;
  bottom: 0px;
  left: 20px;
}
.BoardWrapper .PlayerName4 {
  position: absolute;
  top: 0px;
  left: 20px;
}

.dieRoll {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 40px;
  width: 40px;
  margin-top: -20px;
  margin-left: -20px;
  font-size: 40px;
  line-height: 40px;
  text-align: center;
}

.Board {
  background: #efebe9;
  border: solid 5px #a1887f;
  border-radius: 10px;
  min-width: 320px;
  width: 100%;
  position: relative;
  text-align: center;
  /* margin: 0 auto; */
}
.Cell {
  position: absolute;
  border-radius: 100%;
  border: solid 2px #a1887f;
  background: #bcaaa4;
  height: 20px;
  width: 20px;
}
.Cell.green { border-color: #00c853; }
.Cell.red { border-color: #d50000; }
.Cell.blue { border-color: #2962ff; }
.Cell.yellow { border-color: #ffab00; }
.Cell.over,
.Cell.highlight,
.Piece.over {
	box-shadow: 0 0 20px 5px #ff6d00;
}

.Label {
  position: absolute;
  text-align: center;
  line-height: 1;
  font-family: 'Permanent Marker', cursive;
}

.Piece {
  border-radius: 100%;
  height: 100%;
  width: 100%;
  cursor: not-allowed;
}
.Piece.enabled {
  cursor: pointer;
}
.Piece.green { background: #1b5e20; }
.Piece.yellow { background: #ffab00; }
.Piece.blue { background: #0d47a1; }
.Piece.red { background: #b71c1c; }

.Piece.enabled:hover {
	box-shadow: 0 0 20px 5px #ff6d00;
}
